<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-form :inline="true" :model="search">
          <el-row>
            <el-form-item label="">
              <el-input size="small"  v-model="search.contactPhone"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small"  v-model="search.licenseNo"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small"  v-model="search.shopName"></el-input>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label=":"  size="small">
              <el-date-picker  v-model="search.timeStart" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd"> </el-date-picker>
            </el-form-item>
            <el-form-item label="" size="small" >
              <el-date-picker  v-model="search.timeEnd" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd"> </el-date-picker>
            </el-form-item>
            <el-form-item label=":" size="small" >
              <el-select  v-model="search.unitType" placeholder="">
                <el-option  label="" value=""></el-option>
                <el-option  label="" value="1"></el-option>
                <el-option  label="" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label=":">
              <el-select size="small" v-model="brandCode"  placeholder="" @change="handleBrandChange">
                <el-option label="" value="all" ></el-option>
                <el-option v-for="item in brandList"  :label="item.name" :value="item.code" :key="item.code">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="brandCode !== 'all'">
              <el-select size="small" v-model="search.sn"  placeholder="">
                <el-option v-for="item in (productList[brandCode] || [])" :key="item.sn" :label="item.allName" :value="item.sn">
                </el-option>
              </el-select>
            </el-form-item>
          </el-row>
          <div></div>
          <el-form-item>
            <el-button size="small" type="primary" @click="list"></el-button>
            <el-button size="small" @click="reset"></el-button>
            <el-button  size="small" type="primary" plain  @click="exportData" ></el-button>
          </el-form-item>
        </el-form>
      </el-row>
      <!--  -->
    </el-card>
    <div class="space"></div>
    <el-card>
      <!--  -->
      <el-table :data="tableList" style="width: 100%">
        <el-table-column label="" type="index" align="center">

        </el-table-column>
        <el-table-column prop="licenseNo" label="" align="center"></el-table-column>
        <el-table-column prop="shopName" label="" align="center"></el-table-column>
        <el-table-column prop="addr" label="" align="center"></el-table-column>
        <el-table-column prop="contactName" label="" align="center"></el-table-column>
        <el-table-column prop="contactPhone" label="" align="center"></el-table-column>
        <el-table-column prop="scanOpenid" label="openld" align="center"></el-table-column>
        <el-table-column prop="scanNickname" label="" align="center"></el-table-column>
        <el-table-column prop="scanTime" label="" align="center"></el-table-column>
        <el-table-column prop="prodName" label="" align="center"></el-table-column>
        <el-table-column prop="unitType" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.unitType == 1"></span>
            <span v-if="scope.row.unitType == 2"></span>
          </template>
        </el-table-column>
        <el-table-column prop="rebateValue" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.type == 3">{{ scope.row.rebateValue }}</span>
            <span v-if="scope.row.type == 6"></span>
          </template>
        </el-table-column>
        <el-table-column prop="rebateValue" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.type == 3"></span>
            <span v-if="scope.row.type == 6">{{ scope.row.rebateValue }}</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                     @size-change="sizeChange" @current-change="currentChange"
                     :current-page="search.page" :page-size="search.pageSize"
                     :total="pagination.total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      brandCode:null,
      search: {
        page: 1,
        pageSize: 10,
        contactPhone: '', //
        licenseNo: '', //
        shopName: '', //
        timeStart: '', //
        timeEnd: '', //
        unitType: '', //
        sn: '' //
      },
      pagination: { // 
        total: 0
      },
      tableList: [],
      brandList:[],
      productList:[],
      form: {
        show: false, // 
        id: '',
        name: ''
      }
    }
  },
  created() {
    this.getActSn()
    this.list()
  },
  methods: {
    getActSn(){
      this.$request.post('/saasHbseller/actCommon/actSn', {}, false, res => {
        if (res.code == '200') {
          this.brandList = Object.keys(res.data).map((item)=>{
            if(item === 'hebeizhongyan'){
              return {
                name:'',
                code:item
              }
            }else if(item === 'hebeihehua') {
              return {
                name:'',
                code:item
              }
            }
          })
          this.productList = res.data
          console.log(this.brandList)
          console.log(this.productList)
          return
        }
        this.$message.error(res.msg)
      })
    },
    handleBrandChange(value){
      console.log(value)
      this.search.sn = null
    },
    exportData(){//
      var url = "/saasHbseller/sellerRebate/statics/rebateDetailExportData";
      var xhr = new XMLHttpRequest();
      // var formData = new FormData();
      // console.log(url)
      // for(var attr in this.search) {
      //   formData.append(attr, this.search[attr]);
      // }
      var formData = JSON.stringify(this.search)
      xhr.overrideMimeType("text/plain; charset=x-user-defined");
      xhr.open('POST', url, true);
      xhr.responseType = "blob";
      xhr.responseType = "arraybuffer"
      xhr.setRequestHeader('Content-Type','application/json')
      xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
      xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
      xhr.onload = function(res) {
        if (this.status == 200) {
          var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
          var respHeader = xhr.getResponseHeader("Content-Disposition");
          var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
          if (window.navigator.msSaveOrOpenBlob) {
            navigator.msSaveBlob(blob, fileName);
          } else {
            var link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.download = fileName;
            link.click();
            window.URL.revokeObjectURL(link.href);
          }
        }
      }
      xhr.send(formData);
    },
    currentChange(page) {
      // pageNo
      this.list(event, page);
    },
    sizeChange(pageSize) {
      // pageNo
      this.list(event, null, pageSize);
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
        this.pagination.total = page;
    },
    // 
    reset() {
      this.brandCode = null
      this.search = {
        page: 1,
        pageSize: 10,
        contactPhone: null, //
        licenseNo: null, //
        shopName: null, //
        timeStart: null, //
        timeEnd: null, //
        unitType: null, //
        sn: null //
      }
      this.list();
    },
    // 
    list(_event, page, pageSize) {
      let _pageNo = 1;
      if(page) _pageNo = page;
      this.search.page = _pageNo;
      let _pageSize = 10;
      if(pageSize) _pageSize = pageSize;
      this.search.pageSize = _pageSize;

      this.$request.post('/saasHbseller/sellerRebate/statics/rebateDetail', this.search, true, (res)=>{
          console.log(res.records)
          this.tableList = res.records || [];
          this.initPagination(res.total||0);
      });
    },
    modifyData(actCode, status) {
      this.$confirm(`${status == 0 ? '':''} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = {actCode:actCode, status:status}
        this.$request.post('/saasHbseller/seller/actRank/statModify', params, false, (res)=>{
          if (res.code == '200') {
            this.list();
            this.$message({type: 'success', message: '!'});
          } else {
            this.$message.error(res.msg);
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.space {position:relative;width:100%;height:20px;}
.el-input, .el-select {
  width: 200px;
}
</style>



