<template>
  <div class="review-manage-container">
    <div class="box-container">
      <div class="add-new-seller">
        <el-button type="primary" size="small" style="display:none;margin-bottom:15px "  @click="addNewSeller()"></el-button>
      </div>
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="80px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.authStatus"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in authStatusList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrProvince">
            <el-select
                v-model="filters.addrProvince"
                placeholder=""
                @change="selectBrand1"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrCity">
            <el-select
                v-model="filters.addrCity"
                placeholder=""
                @change="selectBrand2"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrArea">
            <el-select
                v-model="filters.addrArea "
                placeholder=""
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input placeholder="" v-model="filters.keywords" class="input-with-select" style="width: 300px">
              <el-select v-model="filters.searchType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
                <el-option label="" value="salerName"></el-option>
                <el-option label="" value="higherName"></el-option>
                <el-option label="" value="phoneNo"></el-option>
                <el-option label="ID" value="wxOpenId" ></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <div></div>

          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <div class="box-container">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 30px">
        <el-button type="primary" size="small" @click="querySellerFans(1)"></el-button>
        <el-button type="primary" size="small" @click="querySellerFans(2)"></el-button>
        <el-button type="primary" size="small" @click="querySellerFans(3)"></el-button>
        <el-button type="primary" size="small" @click="querySellerFans(3)"></el-button>
        <el-button type="primary" size="small" @click="querySellerFans(3)"></el-button>
      </el-col>
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="newList"
            style="width: 100%">
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="salerName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="phoneNo"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="region"
              width="200"
              label="">
          </el-table-column>
          <el-table-column
              prop="invitCode"
              label="">
          </el-table-column>
          <el-table-column
              prop="higherName"
              label="">
          </el-table-column>
          <el-table-column
              prop="totalIncome"
              label="">
          </el-table-column>
          <el-table-column
              prop="balance"
              width="100"
              label="">
          </el-table-column>
          <el-table-column
              prop="totalPay"
              width="100"
              label="">
          </el-table-column>
          <el-table-column
              prop="teamAcount"
              label="">
          </el-table-column>
          <el-table-column
              prop="ctime"
              label=""
              width="200">
          </el-table-column>
          <el-table-column
              label=""
              width="200"
              prop="appPassTime"
          >
          </el-table-column>
          <el-table-column
              prop="authStatus"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.accountStatus == '1'">{{ "" }}</span>
              <span v-else-if="scope.row.accountStatus == '2'">{{ "" }}</span>
            </template>
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  type="text"
                  size="mini"
                  @click="sellerDetail(scope.row.salerId)"
              ></el-button>
              <el-button
                  type="text"
                  size="mini"
                  v-if="scope.row.accountStatus == '1'"
                  @click="disapproval(scope.row.salerId)"
              ></el-button>
              <el-button
                  type="text"
                  size="mini"
                  v-if="scope.row.accountStatus == '2'"
                  @click="approval(scope.row.salerId)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <!--<el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>-->
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="cancelDialog">
      <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="failReason">
      </el-input>
      <div style="text-align: center;margin-top: 30px">
        <el-button size="small" @click="cancelDialog"> </el-button>
        <el-button size="small" type="primary" @click="disapproval"> </el-button>
      </div>

    </el-dialog>


  </div>
</template>

<script>
  export default {
    name: "dealerMgr",
    // beforeRouteLeave(to, from, next) {
    //   // console.log(to.path)
    //   if (to.path !== '/seller/review/addSeller') {
    //     next()
    //   }else {
    //     next("/seller/review/")
    //   }
    // },
    data(){
      return{
        listLoading:false,

        //
        authStatusList:[
          {
            code:null,
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
        ],
        districtList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
        ],

        //
        cateLvl1List:[],
        cateLvl2List:[],
        cateLvl3List:[],

        filters: {
          //1-2-3-4-
          authStatus :null,
          //  1-2-3-4-5-
          searchType:'',
          //
          keywords:'',
          //
          addrProvince:'',
          //
          addrCity:'',
          //
          addrArea:'',
          //
          orderBy:'1'
        },
        //
        newList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,

        //
        batchPass : true,
        ids:'',

        //
        dialogVisible:false,
        failReason:'',
        sellerIds:''
      }

    },
    created(){
      this.getListNewList()
      this.getOneCategory()
    },
    methods:{
      addNewSeller(){
        this.$router.push({
          'path':'/seller/review/addSeller'
        })
      },
      //1
      getOneCategory(){
        this.$request.post('/fxweb/fxsaas/getProvince',{}, false, (res) => {
          this.cateLvl1List = [...res.data]
        })
      },
      selectBrand1(){
        this.filters.addrCity = ''
        this.cateLvl2List = []
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getTwoCategory()
      },
      //2
      getTwoCategory(){
        if(this.filters.addrProvince  == '')return
        this.$request.post('/fxweb/fxsaas/getCity',{parentCode:this.filters.addrProvince}, false, (res) => {
          this.cateLvl2List = [...res.data]
        })
      },
      selectBrand2(){
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.addrCity == '')return
        this.$request.post('/fxweb/fxsaas/getCounty',{parentCode:this.filters.addrCity}, false, (res) => {
          this.cateLvl3List = [...res.data]
        })
      },
      //
      getListNewList() {
        let params = {
          //
          accountStatus:this.filters.authStatus,
          //
          provinceName: this.filters.addrProvince,
          cityName: this.filters.addrCity,
          areaName: this.filters.addrArea,
          orderBy:this.filters.orderBy,
          pageNo: this.pageNo,
          pageSize: 10,

        };
        if(this.filters.searchType && this.filters.keywords){
          params[this.filters.searchType] = this.filters.keywords
        }
        // 

        this.postSearch(params)
      },
      postSearch(params) {
        // this.listLoading = true;
        this.$request.post('/fxweb/fxsaas/getApprovaledSalers',params,true, (res) => {

            console.log(res.data)
            // this.listLoading = false;
            this.newList = res.data.listData
            this.total = res.data.pageResult.count
            this.pageNo = res.data.pageResult.pageNo

        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      //
      getStatus() {
        //1-2-3-4-
        this.filters.authStatus = '1',
        //  1-2-3-4-5-
        this.filters.searchType = '',
        //
        this.filters.keywords = '',
        //
        this.filters.addrProvince = '',
        //
        this.filters.addrCity = '',
        //
        this.filters.addrArea = '',
        this.filters.orderBy = '1',
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      querySellerFans(orderBy){
        this.filters.orderBy = orderBy
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      inputWithSelectChange(val){
        this.filters.keywords = ""
      },

      //
      approval(salerId){
        let params = {salerId,accountStatus:1}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/fxweb/fxsaas/updateSalers',params,true,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      //
      disapproval(salerId){
        let params = {salerId,accountStatus:2}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/fxweb/fxsaas/updateSalers',params,true,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      cancelDialog(){
        this.dialogVisible = false
        this.failReason = ""
      },

      sellerDetail(salerId){
        this.$router.push({
          path:'/distribution/dealerDetail?salerId='+salerId
        })
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListNewList()
      },
    }
  }
</script>

<style lang="scss" scoped>
  .review-manage-container{
  .box-container{
    background-color: #fff;
    padding: 15px;
    margin-bottom: 30px;
    add-new-seller{
      margin-bottom: 30px;
    }
  &:after{
     content: '';
     display: block;
     width: 0;
     height: 0;
     clear: both;
   }
  }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style >
  .review-manage-container .el-table{text-align: center}
  .review-manage-container .el-table th{text-align: center}
  .review-manage-container .el-table td{text-align: center}
  .review-manage-container .mini .el-form-item__label {width: 40px!important;}
</style>


