<template>
  <div class="review-manage-container">
    <div class="box-container">
      <div class="add-new-seller">
        <el-button type="primary" size="small" style="display:none;margin-bottom:15px "  @click="addNewSeller()"></el-button>
      </div>
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="90px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.authStatus"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in authStatusList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-date-picker
                v-model="filters.time"
                type="datetimerange"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width: 350px">
            </el-date-picker>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input placeholder="" v-model="filters.keywords" class="input-with-select" style="width: 300px">
              <el-select v-model="filters.searchType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
                <el-option label="" value="orderId"></el-option>
                <el-option label="" value="contactName"></el-option>
                <el-option label="" value="contactPhone"></el-option>
                <el-option label="" value="salerName"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          
          <div></div>
          <el-form-item size="small" label="" prop="addrProvince">
            <el-select
                v-model="filters.addrProvince"
                placeholder=""
                @change="selectBrand1"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrCity">
            <el-select
                v-model="filters.addrCity"
                placeholder=""
                @change="selectBrand2"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrArea">
            <el-select
                v-model="filters.addrArea "
                placeholder=""
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.buyNum"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in buyNumList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
            <el-button size="small" class="important" @click="exportData"></el-button>
            <el-button  size="small" ><el-upload
                class="upload-demo"
                action="/fxweb/fxsaas/importOrders"
                :headers="headers"
                :data="addPool"
                :before-upload="handlerSourceFileBUpload"
                :on-success="handleSourceFileSuccess"
                :on-remove="handleSourceFileRemove"
                :file-list="sourceFiles"
                :auto-upload="true">
              <el-button slot="trigger" size="small" type="primary"></el-button>
            </el-upload></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="newList"
            style="width: 100%">
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="orderId"
              label="">
          </el-table-column>
          <el-table-column
              prop="contactName"
              label=""
             >
          </el-table-column>
          <el-table-column
              prop="contactPhone"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="addrDetail"
              label=""
          width="300">
          </el-table-column>
          <el-table-column
              prop="region"
              label=""
              width="200">
          </el-table-column>
          <el-table-column
              prop="buyNum"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="alcoholDegree"
              label=""
              width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.alcoholDegree == 1">{{ "42" }}</span>
              <span v-if="scope.row.alcoholDegree == 2">{{ "52" }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="amount"
              label=""
              width="120">
            <template slot-scope="scope">
              <span>{{ scope.row.amount }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="salerName"
              label="">
          </el-table-column>
          <el-table-column
              prop="desc"
              label="">
          </el-table-column>
          <el-table-column
              prop="ctime"
              label=""
              width="200">
          </el-table-column>

          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  type="text"
                  size="mini"
                  @click="sellerDetail(scope.row.orderId)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="cancelDialog">
      <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="failReason">
      </el-input>
      <div style="text-align: center;margin-top: 30px">
        <el-button size="small" @click="cancelDialog"> </el-button>
        <el-button size="small" type="primary" @click="disapproval"> </el-button>
      </div>

    </el-dialog>


  </div>
</template>

<script>
  export default {
    name: "Reviewmanage",
    // beforeRouteLeave(to, from, next) {
    //   // console.log(to.path)
    //   if (to.path !== '/seller/review/addSeller') {
    //     next()
    //   }else {
    //     next("/seller/review/")
    //   }
    // },
    data(){
      return{
        listLoading:false,

        //
        authStatusList:[
          {
            code:null,
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
          {
            code:"3",
            name:""
          },
          {
            code:"4",
            name:""
          },
          {
            code:"5",
            name:""
          },
        ],
        buyNumList:[
          {
            code:null,
            name:""
          },
          {
            code:"1",
            name:"1"
          },
          {
            code:"2",
            name:"2"
          },
        ],

        //
        cateLvl1List:[],
        cateLvl2List:[],
        cateLvl3List:[],

        filters: {
          //1-2-3-4-
          authStatus :null,
          // : 1-2-3-4-5-6-7-
          commercial:'',
          //: 1-2-
          district:'',
          //  1-2-3-4-5-
          searchType:'',
          //
          keywords:'',
          //
          // appStartTime:'',
          // //
          // appEndTime:'',
          time:[],

          //
          addrProvince:'',
          //
          addrCity:'',
          //
          addrArea:'',

          buyNum:null

        },
        //
        newList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,

        //
        batchPass : true,
        ids:'',

        //
        dialogVisible:false,
        failReason:'',
        sellerIds:'',

        headers: {
          "token": sessionStorage.getItem("access_token"),
          "loginId": sessionStorage.getItem("access_loginId")
        },
        addPool: {

        },
        sourceFiles: []
      }

    },
    created(){
      this.getListNewList()
      this.getOneCategory()
    },
    methods:{
      addNewSeller(){
        this.$router.push({
          'path':'/seller/review/addSeller'
        })
      },
      //1
      getOneCategory(){
        this.$request.post('/lsh/seller-manager/region/province',{}, true, (res) => {
          this.cateLvl1List = [...res]
        })
      },
      selectBrand1(){
        this.filters.addrCity = ''
        this.cateLvl2List = []
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getTwoCategory()
      },
      //2
      getTwoCategory(){
        if(this.filters.addrProvince  == '')return
        this.$request.post('/lsh/seller-manager/region/newCity',{parentCode:this.filters.addrProvince}, true, (res) => {
          this.cateLvl2List = [...res]
        })
      },
      selectBrand2(){
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.addrCity == '')return
        this.$request.post('/lsh/seller-manager/region/newDistrict',{parentCode:this.filters.addrCity}, true, (res) => {
          this.cateLvl3List = [...res]
        })
      },
      //
      getListNewList() {
        let params = {
          //
          orderStatus:this.filters.authStatus,
          buyNum:this.filters.buyNum,
          //
          // keywords:this.filters.keywords,

          //
          provinceId: this.filters.addrProvince,
          cityId: this.filters.addrCity,
          areaId: this.filters.addrArea,
          //
          startTime: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          endTime: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',

          pageNo: this.pageNo,
          pageSize: 10,

        };
        if(this.filters.searchType&&this.filters.keywords){
          params[this.filters.searchType] = this.filters.keywords
        }
        this.postSearch(params)
      },
      postSearch(params) {
        // this.listLoading = true;
        this.$request.post('/fxweb/fxsaas/getOrders', params, true, (res) => {

            console.log(res.data)
            // this.listLoading = false;
            this.newList = res.data.listData
            this.total = res.data.pageResult.count
            this.pageNo = res.data.pageResult.pageNo

        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      //
      getStatus() {
        //1-2-3-4-
        this.filters.authStatus = null,
        // : 1-2-3-4-5-6-7-
        this.filters.commercial = '',
        //: 1-2-
        this.filters.district = '',
        //  1-2-3-4-5-
        this.filters.searchType = '',
        //
        this.filters.keywords = '',

        this.filters.time = [];
        //
        this.filters.appStartTime = '',
        //
        this.filters.appEndTime = '',

        //
        this.filters.addrProvince = '',
        //
        this.filters.addrCity = '',
        //
        this.filters.addrArea = '',

        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      inputWithSelectChange(val){
        this.filters.keywords = ""
      },

      //
      approval(sellerIds){
        let params = {sellerIds,authResult:1}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/approvalBatch',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      //
      disapprovalDialog(sellerIds){
        this.dialogVisible = true
        this.sellerIds = sellerIds
      },
      disapproval(){
        let params = {sellerIds:this.sellerIds,authResult:2,failReason:this.failReason}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/approvalBatch',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }
          })
        })
      },
      cancelDialog(){
        this.dialogVisible = false
        this.failReason = ""
      },
      sellerDetail(orderId){
        this.$router.push({
          path:'/distribution/orderDetail?orderId='+orderId
        })
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListNewList()
      },

      exportData(){//
        var url = "/fxweb/fxsaas/exportOrders";
        var xhr = new XMLHttpRequest();
        var formData = new FormData();
        let params = {
          //
          provinceId: this.filters.addrProvince,
          cityId: this.filters.addrCity,
          areaId: this.filters.addrArea,
          //
          startTime: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          endTime: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',
        };
        if(this.filters.searchType&&this.filters.keywords){
          params[this.filters.searchType] = this.filters.keywords
        }
        for(var attr in params) {
          formData.append(attr, params[attr]);
        }
        xhr.overrideMimeType("text/plain; charset=x-user-defined");
        xhr.open('POST', url, true);
        xhr.responseType = "blob";
        xhr.responseType = "arraybuffer"
        xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
        xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
        xhr.onload = function(res) {
          if (this.status == 200) {
            var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
            var respHeader = xhr.getResponseHeader("Content-Disposition");
            var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
            if (window.navigator.msSaveOrOpenBlob) {
              navigator.msSaveBlob(blob, fileName);
            } else {
              var link = document.createElement('a');
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              window.URL.revokeObjectURL(link.href);
            }
          }
        }
        xhr.send(formData);
      },
      handlerSourceFileBUpload(file) {
      },
      // 
      handleSourceFileSuccess(res, file) {
        if(res.ok) {
          this.$message({type:'success', message:''});
        } else {
          this.$message.error(res.msg);
        }
      },
      // 
      handleSourceFileRemove(file, fileList) {
        this.sourceFiles = []; // 
      },
    }
  }
</script>

<style lang="scss" scoped>
  .review-manage-container{
  .box-container{
    background-color: #fff;
    padding: 15px;
    margin-bottom: 30px;
    add-new-seller{
      margin-bottom: 30px;
    }
  &:after{
     content: '';
     display: block;
     width: 0;
     height: 0;
     clear: both;
   }
  }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style >
  .review-manage-container .el-table{text-align: center}
  .review-manage-container .el-table th{text-align: center}
  .review-manage-container .el-table td{text-align: center}
  .review-manage-container .mini .el-form-item__label {width: 40px!important;}
</style>


