<template>
  <div class="sign-container">
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span>3</span>
      </div>
      <el-form>
        <el-form-item label=""></el-form-item>
        <el-form-item v-for="(item,index) in list" :key="index" label=''>
         {{ item.snName }} 
          <el-input-number v-model="item.points" :precision="0" :min="0" controls-position="right"></el-input-number>
          
<!--          <span class="ml20">-->
<!--            -->
<!--          </span>-->
        </el-form-item>
      </el-form>
    </el-card>
    <div style="height: 30px"></div>
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div style="margin-bottom: 20px">:<el-button size="" style="margin-left: 20px"  @click="getList(3)"></el-button></div>
      <el-form>
        <el-form-item v-for="(item,index) in hb" :key="index" label=''>
           <el-input-number v-model="item.awardPrice" :disabled="item.id ? true : false" :precision="2" :min="0" controls-position="right"></el-input-number>
          <span style="margin-right: 20px"></span>
           <el-input-number v-model="item.totalNum" :disabled="item.id ? true : false" :precision="0" :min="0" controls-position="right"></el-input-number>

          <span v-if="item.id ? true : false">
               <span style="margin-right: 20px"></span>
            {{ item.totalNum - item.outNum }}
            </span>
          <span style="margin-right: 20px"></span>
          :{{ parseFloat((item.awardPrice*item.totalNum).toPrecision(12))  }}
          <span style="margin-right: 20px"></span>
           <el-input-number v-model="item.prizePert" :precision="1" :step="0.1" :min="0" controls-position="right"></el-input-number>
          %
          <span v-if="item.id ? true : false">
              <span style="margin-right: 20px"></span>
              <el-button type='primary' @click="addRepertory(item)"></el-button>
            </span>
          <span style="margin-right: 20px"></span>
          <el-button type='danger' @click="deleteAward(item,index)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <div style="height: 30px"></div>
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <el-form>
        <el-form-item label=""></el-form-item>
        <el-form-item label="5">
          
          <el-input-number v-model="form.contDay5" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
        <el-form-item label="6">
          
          <el-input-number v-model="form.contDay6" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
        <el-form-item label="7">
          
          <el-input-number v-model="form.contDay7" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
      </el-form>
    </el-card>
    <div style="height: 40px;text-align: center;margin-top: 30px">
      <el-button type="primary" @click="handleSave"></el-button>
    </div>


    <el-dialog :title="title" :visible.sync="listVisible" width="800px">
      <el-table :data="awdList" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="selectPrize(scope.row,title)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handSizeChange" @current-change="handCurrentChange" :current-page="params.pageNo" :page-size="params.pageSize" layout="total, prev, pager, next, jumper" :total="listTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "SignMgr",
    data(){
      return {
        list:[
          {
            sn:'1234567',
            snName:'',
            points:0
          },
          {
            sn:'11111111010123456',
            snName:'Test',
            points:0
          },
          {
            sn:'6901028250009',
