          },
          {
            sn:'6901028250382',
            snName:'',
            points:0
          },
          {
            sn:'6901028250234',
            snName:'',
            points:0
          },
          {
            sn:'6901028250351',
            snName:'',
            points:0
          },
          {
            sn:'1111111111111',
            snName:'',
            points:0
          },
          {
            sn:'1111111111112',
            snName:'',
            points:0
          },
          {
            sn:'1111111111114',
            snName:'',
            points:0
          },
          {
            sn:'6901028078245',
            snName:'',
            points:0
          },
          {
            sn:'6901028079075',
            snName:'',
            points:0
          },
          {
            sn:'6901028250788',
            snName:'',
            points:0
          },
          {
            sn:'1111111111115',
            snName:'',
            points:0
          },
          {
            sn:'6901028077699',
            snName:'',
            points:0
          },
          {
            sn:'6901028080019',
            snName:'',
            points:0
          },
          {
            sn:'6901028250757',
            snName:'2',
            points:0
          },
          {
            sn:'6901028079815',
            snName:'',
            points:0
          },
          {
            sn:'6901028250320',
            snName:'',
            points:0
          },
          {
            sn:'6901028250412',
            snName:'',
            points:0
          },
          {
            sn:'6901028250979',
            snName:'',
            points:0
          },
          {
            sn:'6901028250597',
            snName:'',
            points:0
          },
          {
            sn:'6901028107105',
            snName:'',
            points:0
          },
          {
            sn:'6901028250955',
            snName:'',
            points:0
          },
          {
            sn:'6901028107198',
            snName:'',
            points:0
          },
          {
            sn:'6901028250790',
            snName:'',
            points:0
          },
          {
            sn:'6901028077460',
            snName:'()',
            points:0
          },
          {
            sn:'6901028107280',
            snName:'',
            points:0
          },
          {
            sn:'6901028107259',
            snName:'',
            points:0
          },
          {
            sn:'6901028107341',
            snName:'',
            points:0
          },
          {
            sn:'6901028107471',
            snName:'',
            points:0
          },
          {
            sn:'6901028107464',
            snName:'',
            points:0
          },
          {
            sn:'6901028107440',
            snName:'',
            points:0
          },
          {
            sn:' 6901028107433',
            snName:' ',
            points:0
          },
          {
            sn:'6901028107501',
            snName:'',
            points:0
          },
          {
            sn:'6901028107495',
            snName:'',
            points:0
          },
          {
            sn:'6901028107358',
            snName:'',
            points:0
          },
        ],
        hb:[],
        form:{
          num:0,
          contDay5:0,
          contDay6:0,
          contDay7:0,
        },
        title: '',
        awdList: [],
        params: {
          metraFlag: '',
          pageNo: 1,
          pageSize: 10,
          status: 1
        },
        listTotal: 0,
        listVisible: false,

        defaultAwae: { //  
          "awardPrice": 0,
          "prizePert": 0,
          "totalNum": 0,
          "awardPic": "",
        },
      }
    },
    created() {
      this.getScanConf()
      this.getContConf()
      this.getRedpackConf()
    },
    mounted() {
    },
    methods:{
      getList(type) {
        if (type == '1') {
          this.params.metraFlag = 'object'
          this.title = ''
        } else if (type == '2') {
          this.params.metraFlag = 'virtual'
          this.title = ''
        } else if (type == '3') {
          this.params.metraFlag = 'redpack'
          this.title = ''
        } else if (type == '6') {
          this.params.metraFlag = 'integral'
          this.title = ''
        } else if (type == '7') {
          this.params.metraFlag = 'cdDisc'
          this.title = ''
        } else if (type == '8') {
          this.params.metraFlag = 'cdDouble'
          this.title = ''
        }
        this.$request.post('/api/wiseqr/metra/list', this.params, true, res => {
          if (res.ret === '200000') {
            this.awdList = []
            this.awdList = res.data.list
            this.listTotal = res.data.page.count
            this.listVisible = true
            return
          }
          this.$message.error(res.message)
        })
      },
      addRepertory(item) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{1,}$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          if (value == 0) return this.$message.error('0')
          this.$request.post('/hbact/dayscan/redpack/stock', {
            id: item.id ,
            addNum : value
          }, false, res => {
            if (res.code == '200') {
              this.$message.success('')
              this.getRedpackConf()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      // 
      selectPrize(obj,title) {
        let newAwae = JSON.parse(JSON.stringify(this.defaultAwae))
        newAwae.awardPic = obj.pic
        this.hb.push(newAwae)
        this.listVisible = false
      },
      deleteAward(item,index){
        this.$request.post('/hbact/dayscan/redpack/delete', {
          id: item.id ,
        }, false, res => {
          if (res.code == '200') {
            this.$message.success('')
            this.getRedpackConf()
          } else {
            this.$message.error(res.message)
          }
        })
      },
      handSizeChange(newSize) {
        this.params.pageSize = newSize
        this.getList()
      },
      handCurrentChange(newSize) {
        this.params.pageNo = newSize
        this.getList()
      },
      // 
      getScanConf(){
        this.$request.post('/hbact/dayscan/conf/list', {}, true, res => {
          if (res.code == '200') {
              this.list.forEach((e)=>{
                let data = res.data
                data.forEach((i)=>{
                  if(e.sn == i.sn){
                    e.points = i.points
                  }
                })
              })
            // console.log(res.data)
            return
          }
          this.$message.error(res.msg)
        })
      },
      // 
      getContConf(){
        this.$request.post('/hbact/dayscan/cont/list', {}, true, res => {
          if (res.code == '200') {
            let data = res.data
            data.forEach((e,i)=>{
              if(e.contDay == 5){
                this.form.contDay5 = e.points
              }
              else if(e.contDay == 6) {
                this.form.contDay6 = e.points
              }
              else if(e.contDay == 7) {
                this.form.contDay7 = e.points
              }
            })
            return
          }
          this.$message.error(res.msg)
        })
      },
      getRedpackConf(){
        this.$request.post('/hbact/dayscan/redpack/query', {}, true, res => {
          if (res.code == '200') {
            this.hb = res.data
            return
          }
          this.$message.error(res.msg)
        })
      },
      //
      dayscanUpdate(){
        return new Promise ((resolve,reject)=>{
          let params = this.list.map((e,i)=>{
            return {
              "sn": e.sn,
              "snName": e.snName,
              "points": e.points
            }
          })
          this.$request.post('/hbact/dayscan/conf/update', params, true, res => {
            resolve(res)
          })
        })
      },
      //
      contDayUpdate(){
        return new Promise ((resolve,reject)=>{
          this.$request.post('/hbact/dayscan/cont/update', [
            {
              "contDay":5,
              "points": this.form.contDay5
            },
            {
              "contDay":6,
              "points": this.form.contDay6
            },
            {
              "contDay":7,
              "points": this.form.contDay7
            }
          ], true, res => {
            resolve(res)
          })
        })
      },
      setRedpack(){
        return new Promise ((resolve,reject)=>{
          let params = this.hb.map( e =>{
            if(e.id){
              return {
                id:e.id,
                awardPrice:e.awardPrice,
                prizePert:e.prizePert,
                totalNum:e.totalNum,
                awardPic:e.awardPic
              }
            }else{
              return {
                awardPrice:e.awardPrice,
                prizePert:e.prizePert,
                totalNum:e.totalNum,
                awardPic:e.awardPic
              }
            }
          })
          this.$request.post('/hbact/dayscan/redpack/set', params, true, res => {
            resolve(res)
          })
        })
      },
      handleSave(){
        Promise.all([
          this.dayscanUpdate(),
          this.contDayUpdate(),
          this.setRedpack()
        ])
        .then(([dayscan, contDay,setRedpack]) => {
          if (dayscan.code == '200') {
            console.log(dayscan.data)
          }else {
            this.$message.error(dayscan.msg)
            return
          }
          if (contDay.code == '200') {
            console.log(contDay.data)
          }else {
            this.$message.error(contDay.msg)
            return
          }
          if (setRedpack.code == '200') {
            console.log(setRedpack.data)
          }else {
            this.$message.error(setRedpack.msg)
            return
          }
          this.$message.success('')
          this.getScanConf()
          this.getContConf()
          this.getRedpackConf()
        })
      }
    }

  }
</script>

<style lang="scss" scoped>
  .sign-container{
    background: #fff;
    padding: 30px;
    box-sizing: border-box;

  }
</style>



