<template>
  <div class="container">
    <el-card v-loading="loading">
      <el-col class="mb20">
        <el-button type="primary" @click="open" :disabled="!canOpenAward"></el-button>
        <span style="margin-left: 20px"></span>
        <span v-if="info.openStatus == 1"></span>
        <span v-if="info.openStatus == 2"></span>
        <span v-if="info.openStatus == 3"></span>
        <span v-if="info.openStatus == 4"></span>
      </el-col>
      <el-col class="mb20">
        <div><span style="color: #409EFF">{{ openawdUserNum }}</span></div>
      </el-col>
      <el-col class="mb20">
        <el-card :body-style="{ padding: '20px' }">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-form :model="info" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label="DIYID" prop="actCode">
              <el-input :disabled="!!info.id" v-model="info.actCode" style="width: 220px"></el-input>
            </el-form-item>
            <el-form-item label="DIY" prop="joinEtime">
              <el-date-picker :disabled="!!info.id" v-model="info.joinEtime"  value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="openRedpack">
              <el-input-number :disabled="info.openStatus > 1" :step="0.01" :precision="2":min="0" controls-position="right" v-model="info.openRedpack"></el-input-number> 
            </el-form-item>
            <el-form-item label="" prop="rcdNote">
              <el-input :disabled="!!info.id" type="textarea" v-model="info.rcdNote" style="width: 220px"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

    </el-card>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading:true,
        info:{
          "id": null, //  null  
          "joinEtime": "", // 
          "actCode": "", // code
          "openRedpack": 0 ,// 
          "openStatus": 0,
          "rcdNote":""
        },
        addNum :0,
        openawdUserNum:0,
        canOpenAward:false,
        rules: {
          actCode: [{required: true, message: 'ID', trigger: 'blur'}],
          joinEtime: [{required: true, message: '', trigger: 'blur'}],
          openRedpack: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    created() {
      this.getInfo()
    },
    methods: {
      getInfo() {
        this.$request.post('/hbact/saas/diy/configInfo', {}, true, res => {
          this.loading = false
          if (res.code == '200') {
            console.log(res.data)
            if(res.data.nowConfig){
              this.info = res.data.nowConfig
            }
            this.canOpenAward = res.data.canOpenAward
            this.openawdUserNum = res.data.openawdUserNum
            return
          }
          this.$message.error(res.msg)
        })
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.save()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      save(){
        this.loading = true
        let params = {
          "id": this.info.id,
          "joinEtime":  this.info.joinEtime,
          "actCode": this.info.actCode,
          "confCode": "diy_act",
          "openRedpack": this.info.openRedpack,
          "rcdNote": this.info.rcdNote,
        }
        this.$request.post('/hbact/saas/diy/configUpdate', params, true, res => {
          this.loading = false
          if (res.code == '200') {
            this.$message.success("")
            this.getInfo()
            return
          }
          this.$message.error(res.msg)
        })
      },
      open(){
        this.$request.post('/hbact/saas/diy/awardOpen', {configId:this.info.id}, false, res => {
          if (res.code == '200') {
            this.$message.success('')
            this.getInfo()
            return
          }
          this.$message.error(res.msg)
          this.getInfo()
        })
      },
    }
  };
</script>
<style lang="scss" scoped>
  .select-num-dialog {
    .con {
      text-align: center;
      user-select: none;
      .warpper {
        padding: 20px 40px;
      }
      .num {
        display: inline-block;
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        border: 1px solid #ccc;
        border-radius: 50%;
        margin: 10px 10px;
        cursor: pointer;
        transition: all 0.3s;
        &.active {
          color: #fff;
          background-color: #409EFF;
        }
        &.active:hover {
          color: #fff;
        }
      }
      .num:hover {
        color: #409EFF;
        border: 1px solid #409EFF;
      }
    }
  }
  .selected-dialog {
    .con {
      width: 400px;
      height: 60px;
      display: flex;
      margin: 0 auto 40px;
      .num {
        width: 60px;
        height: 60px;
        line-height: 60px;
        text-align: center;
        color: #fff;
        background-color: #409EFF;
        border-radius: 50%;
      }
      .num:first-child {
        margin: 0 40px 0 120px;
      }
    }
    .btn {
      text-align: center;
    }
  }
</style>



