<template>
  <div class="sign-container">
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span style="font-size: 18px;font-weight: bolder"></span>
      </div>
      <el-form>
        <el-form-item>
          <span></span>
          <el-date-picker
              v-model="exStime"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              format="yyyy-MM-dd HH:mm:ss"
              placeholder="">
          </el-date-picker>
          <span></span>
          <el-date-picker
              v-model="exEtime"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              format="yyyy-MM-dd HH:mm:ss"
              placeholder="">
          </el-date-picker>
          <el-button size="small" type="primary" style="margin-left: 20px"  @click="configTime"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <div v-for="(item,index) in list" :key="index" >
      <div style="height: 30px"></div>
      <el-card :body-style="{ padding: '20px' }">
        <div slot="header" class="clearfix">
          <span style="font-size: 18px;font-weight: bolder">{{ index+1 }}20-25</span>
        </div>
        <el-form>
          <el-form-item label=''>
            {{ item.cardConf.name }}
            <span style="margin-right: 20px"></span>
            <el-input-number v-model="item.cardConf.totalNum" disabled :precision="0" :min="0" controls-position="right"></el-input-number>
            <span style="margin-right: 20px"></span>
            {{ item.cardConf.totalNum - item.cardConf.outNum }}
            <span style="margin-right: 20px"></span>
            <el-input-number v-model="item.cardConf.prizePert" :precision="1" :step="0.1" :min="0" controls-position="right"></el-input-number>
            %
            <span style="margin-right: 20px"></span>
            <el-button type="primary" @click="addCardNum(item.cardConf.code)"></el-button>
          </el-form-item>
          <template v-for="(ai,key) in item.awardConf" >
            <div  style="height: 30px;font-size: 18px;line-height: 30px;margin: 30px 0;color: #409EFF">{{ key + 1 }}:</div>
            <el-form-item label=":">
              <el-select :disabled="!!ai.id" @change="handleSelectChange(ai)" v-model="ai.awardType"  placeholder="">
                <el-option
                    v-for="i in awardType"
                    :key="i.value"
                    :label="i.label"
                    :value="i.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-if="ai.awardType&&!ai.id">
              <div>{{ ai.awardType == '1' ? '' : ai.awardType == '3' ? '' : '' }}:
                <el-button size="" style="margin-left: 20px"  @click="getList(ai.awardType,index,key)"></el-button>
              </div>
            </el-form-item>
            <el-form-item v-if="ai.awardType&&ai.awardName">
                <span v-if="ai.awardType !== 1 " style="margin-right: 20px">: {{ ai.awardName }}</span>
                {{ ai.awardType == 3 || ai.awardType == 6 ? ":" : ":" }}
                <el-input-number v-if="ai.awardType == 3 || ai.awardType == 6" v-model="ai.awardPrice" :disabled="!!ai.id ? true : false" :precision="2" :min="0" controls-position="right"></el-input-number>
                {{ ai.awardType == 1 ? ai.awardName : ai.awardType == 3 ? "" : "" }}
                <span style="margin-right: 20px"></span>

              <el-input-number v-model="ai.totalNum" :disabled="ai.id ? true : false" :precision="0" :min="0" controls-position="right"></el-input-number>
              <span v-if="ai.id ? true : false">
                <span style="margin-right: 20px"></span>
                {{ ai.totalNum - ai.outNum }}
              </span>
              <span style="margin-right: 20px"></span>
              <span v-if="ai.awardType == 3">:{{ parseFloat((ai.awardPrice*ai.totalNum).toPrecision(12))  }}</span>
              <span v-if="ai.awardType == 6">:{{ ai.awardPrice*ai.totalNum }}</span>
              <span style="margin-right: 20px"></span>
              <span v-if="ai.id ? true : false">
                <span style="margin-right: 20px"></span>
                <el-button type="primary" @click="addRepertory(ai.id)"></el-button>
              </span>
              <span v-if="ai.id ? true : false">
                <span style="margin-right: 20px"></span>
                <el-button type='danger' @click="deleteAward(ai.id)"></el-button>
              </span>
              <span v-if="ai.id ? true : false">
                <span style="margin-right: 20px"></span>
                <el-button type='danger' @click="forbiddenAward(ai.id)"></el-button>
              </span>
            </el-form-item>
          </template>
        </el-form>
        <div>
          <div style="height: 30px;font-size: 18px;line-height: 30px;margin: 60px 0 30px;color: red"></div>
          <div v-for="(bi,i) in item.banConf" :key="i" style="height: 30px;font-size: 16px;line-height: 30px;margin: 30px 0">
            <span style="margin-right: 20px">:{{ bi.awardName }}</span>
            <span style="margin-right: 20px">:{{ bi.awardType == 1 ? "" : bi.awardType == 3 ? "" : "" }}</span>
            <span style="margin-right: 20px">:{{ bi.totalNum }}</span>
            <span style="margin-right: 20px" v-if="bi.awardType == 3 || bi.awardType == 6">:{{ bi.awardPrice }}{{ bi.awardType == 1 ? bi.awardName : bi.awardType == 3 ? "" : "" }}</span>
            <span style="margin-right: 20px">:{{ bi.totalNum - bi.outNum }}</span>
            <span style="margin-right: 20px" v-if="bi.awardType == 3">:{{ parseFloat((bi.awardPrice*bi.totalNum).toPrecision(12))  }}</span>
            <span style="margin-right: 20px" v-if="bi.awardType == 6">:{{ bi.awardPrice*bi.totalNum }}</span>
          </div>
        </div>
      </el-card>
      <div style="height: 30px"></div>
    </div>

    <div style="height: 40px;text-align: center;margin-top: 30px">
      <el-button type="primary" @click="handleSave"></el-button>
    </div>


    <!--  -->
    <el-dialog :title="title" :visible.sync="listVisible" width="800px" @close="handleColseList">
      <el-table :data="metraList" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="selectPrize(scope.row,title,cardNo,awardNo)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handSizeChange" @current-change="handCurrentChange" :current-page="params.pageNo" :page-size="params.pageSize" layout="total, prev, pager, next, jumper" :total="listTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "CardColletingMgr",
    components: {},

    data(){
      return {
        exStime:'',
        exEtime:'',
        list:[],
        form:{
          num:0,
          contDay5:0,
          contDay6:0,
          contDay7:0,
        },
        awardType:[
          {
            value: 1,
            label:''
          },
          {
            value: 3,
            label:''
          },
          {
            value: 6,
            label:''
          },
        ],
        awardDemo:{
          "awardType": null,
          "awardName": "",
          "awardPrice": null,
          "awardPic": "",
          "totalNum": null,
        },
        title: '',
        metraList: [],
        params: {
          metraFlag: '',
          pageNo: 1,
          pageSize: 10,
          status: 1
        },
        listTotal: 0,
        listVisible: false,

        cardNo: null,
        awardNo: null
      }
    },
    created() {
      this.getDetail()
    },
    mounted() {
    },
    methods:{
      handleSelectChange(item){
        console.log(item)
        item.awardName = ""
        item.awardPic = ""
        item.totalNum = null
      },
      // 
      getDetail(){
        this.$request.post('/hbact/commucard/saas/queryConfig', {}, true, res => {
          if (res.code == '200') {
            this.exStime = res.data.exConfig.exStime || ""
            this.exEtime = res.data.exConfig.exEtime || ""
            this.list = res.data.mainConfig
            this.list.map((item,index)=>{
              if(item.awardConf.length < 3){
                let length = item.awardConf.length
                for(let i = 0 ; i < 3 ; i++){
                  if(i > length - 1){
                    item.awardConf.push(
                      {
                        "awardType": null,
                        "awardName": "",
                        "awardPrice": null,
                        "awardPic": "",
                        "totalNum": null,
                      }
                    )
                  }
                }
              }
            })
            return
          }
          this.$message.error(res.msg)
        })
      },

      //
      dayscanUpdate(){
        let params = this.list.map((e,i)=>{
          let awardConf = []
            e.awardConf.forEach((ele,i)=>{
              if(ele.awardType){
                if(ele.awardName){
                  if(ele.awardPrice){
                    if(ele.totalNum>0){
                      awardConf.push(ele)
                    }else {
                      if(ele.id){
                        awardConf.push(ele)
                      }else {
                        this.$message.error("0")
                        throw '1!'
                      }
                    }
                  }else {
                    if(ele.awardPrice == 0){
                      this.$message.error("0")
                      throw '2!'
                    }
                    this.$message.error("")
                    throw '2!'
                  }
                }else {
                  this.$message.error("")
                  throw '3!'
                }
              }
            })
          return {
            cardConf:e.cardConf,
            awardConf
          }
        })
        console.log(params)
        return new Promise ((resolve,reject)=>{
          this.$request.post('/hbact/commucard/saas/setConfig', params, true, res => {
            resolve(res)
          })
        })
      },

      handleSave(){
        Promise.all([
          this.dayscanUpdate(),
        ])
        .then(([dayscan]) => {
          if (dayscan.code == '200') {
            console.log(dayscan.data)
          }else {
            this.$message.error(dayscan.msg)
            return
          }
          this.$message.success('')
          this.getDetail()
        })
      },

      configTime(){
        this.$request.post('hbact/commucard/saas/exchange/config', { exStime:this.exStime,exEtime:this.exEtime }, false, res => {
          if (res.code === '200') {
            this.$message.success('')
          } else {
            this.$message.error(res.message)
          }
        })
      },

      getList(type,index,key) {
        if (type == '1') {
          this.params.metraFlag = 'object'
          this.title = ''
        } else if (type == '2') {
          this.params.metraFlag = 'virtual'
          this.title = ''
        } else if (type == '3') {
          this.params.metraFlag = 'redpack'
          this.title = ''
        } else if (type == '6') {
          this.params.metraFlag = 'integral'
          this.title = ''
        } else if (type == '7') {
          this.params.metraFlag = 'cdDisc'
          this.title = ''
        }
        if(index != -1){
          this.cardNo = index
        }
        if(key != undefined){
          if(key != -1){
            this.awardNo = key
          }
        }
        this.$request.post('/api/wiseqr/metra/list', this.params, true, res => {
          if (res.ret === '200000') {
            this.metraList = []
            this.metraList = res.data.list
            this.listTotal = res.data.page.count
            this.listVisible = true
            return
          }
          this.$message.error(res.message)
        })
      },
      // 
      selectPrize(obj,title,cardNo,awardNo) {
        // let newAwae = JSON.parse(JSON.stringify(this.defaultAwae))
        // newAwae.awardPic = obj.pic
        // newAwae.poolName = obj.name
        // newAwae.prizeName = obj.name
        // newAwae.poolId = obj.id
        let card = this.list[cardNo]
        console.log(this.list)
        card.awardConf[awardNo].awardName = obj.name
        card.awardConf[awardNo].awardPrice = obj.marketMoney
        card.awardConf[awardNo].awardPic = obj.pic
        this.handleColseList()
      },
      handleColseList(){
        this.listVisible = false
        this.params.pageNo = 1
      },
      deleteAward(id){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/hbact/commucard/saas/delete', {id}, false, res => {
            if (res.code === '200') {
              this.$message.success('')
              this.getDetail()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {

        })

      },
      forbiddenAward(id){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/hbact/commucard/saas/ban', {id}, false, res => {
            if (res.code === '200') {
              this.$message.success('')
              this.getDetail()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {

        });
      },
      handSizeChange(newSize) {
        this.params.pageSize = newSize
        this.getList()
      },
      handCurrentChange(newSize) {
        this.params.pageNo = newSize
        this.getList()
      },
      addCardNum(code) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{1,}$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          if (value == 0) return this.$message.error('0')
          this.$request.post('/hbact/commucard/saas/card/add', {
            code,
            addNum: value
          }, false, res => {
            if (res.code === '200') {
              this.$message.success('')
              this.getDetail()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      addRepertory(id) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{1,}$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          if (value == 0) return this.$message.error('0')
          this.$request.post('/hbact/commucard/saas/stock/add', {
            id,
            addNum: value
          }, false, res => {
            if (res.code === '200') {
              this.$message.success('')
              this.getDetail()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
    }

  }
</script>

<style lang="scss" scoped>
  .sign-container{
    background: #fff;
    padding: 30px;
    box-sizing: border-box;
  }
</style>



