<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-form :inline="true" :model="search">
          <el-form-item label=":" >
            <el-select  v-model="search.status" placeholder="">
              <el-option  label="" value="1"></el-option>
              <el-option  label="" value="2"></el-option>
              <el-option  label="" value="3"></el-option>
              <el-option  label="" value="4"></el-option>
              <el-option  label="" value="5"></el-option>
              <el-option  label="" value="6"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input size="small"  v-model="search.contactPhone"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input size="small"  v-model="search.licenseNo"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
                v-model="search.txTime"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
                v-model="search.optFinishTime"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="">
            </el-date-picker>
          </el-form-item>
          <div></div>
          <el-form-item>
            <el-button size="small" type="primary" @click="list"></el-button>
            <el-button size="small" @click="reset"></el-button>
            <el-button size="small" type="primary" @click="handleClickReTry"></el-button>
            <el-button  size="small" type="primary" plain  @click="exportData" ></el-button>
          </el-form-item>
        </el-form>
      </el-row>
      <!--  -->
    </el-card>
    <div class="space"></div>
    <el-card>
      <!--  -->
      <el-table :data="tableList"  @selection-change="handleSelectionChange" style="width: 100%">
        <el-table-column
            v-if="showSelection"
            type="selection"
            width="55">
        </el-table-column>
        <el-table-column label="" type="index" align="center"></el-table-column>
        <el-table-column label="id" prop="id" align="center"></el-table-column>
        <el-table-column prop="licenseNo" label="" align="center"></el-table-column>
        <el-table-column prop="addr" label="" align="center"></el-table-column>
        <el-table-column prop="contactName" label="" align="center"></el-table-column>
        <el-table-column prop="contactPhone" label="" align="center"></el-table-column>

        <el-table-column prop="rebateNum" label="" align="center">
          <template slot-scope="scope">
<!--            {{ scope.row.txAmount }}-->
          </template>
        </el-table-column>
        <el-table-column prop="txAmount" label="" align="center">
          <template slot-scope="scope">
            {{ scope.row.txAmount }}
          </template>
        </el-table-column>
        <el-table-column prop="txTime" label="" align="center"></el-table-column>
        <el-table-column prop="optFinishTime" label="" align="center"></el-table-column>
        <el-table-column prop="status" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status == 1"></span>
            <span v-if="scope.row.status == 2"></span>
            <span v-if="scope.row.status == 3"></span>
            <span v-if="scope.row.status == 4"> </span>
            <span v-if="scope.row.status == 5"></span>
            <span v-if="scope.row.status == 6"></span>
          </template>
        </el-table-column>
        <el-table-column
            fixed="right"
            label=""
            width="150"
        >
          <template slot-scope="scope" >
            <el-button
                v-if="scope.row.status == '1'"
                type="text"
                size="mini"
                @click="approval(scope.row.id)"
            ></el-button>
            <el-button
                v-if="scope.row.status == '1'"
                type="text"
                size="mini"
                @click="disapprovalDialog(scope.row.id)"
            ></el-button>
            <el-popover
                v-if="scope.row.status == '5' || scope.row.status == '6'"
                placement="bottom"
                title=""
                width="200"
                trigger="click"
                :content="scope.row.errMsg">
              <el-button
                  slot="reference"
                  type="text"
                  size="mini"
              ></el-button>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-col :span="24" class="footer-toolbar" style="height: 50px">
        <el-button
            v-if="showSelection"
            type="primary"
            size="small"
            :disabled="batchOperations"
            @click="modifyAuthOrder(ids,true)"
            style="float: left"
        ></el-button>
        <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                       @size-change="sizeChange" @current-change="currentChange"
                       :current-page="search.page" :page-size="search.pageSize"
                       :total="pagination.total">
        </el-pagination>
      </el-col>
    </el-card>

    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="cancelDialog">
      <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="failReason">
      </el-input>
      <div style="text-align: center;margin-top: 30px">
        <el-button size="small" @click="cancelDialog"> </el-button>
        <el-button size="small" type="primary" @click="disapproval"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showSelection:false,
      search: {
        page: 1,
        pageSize: 10,
        contactPhone: "", //
        licenseNo: "", //
        status:null,
        txTime:null, //
        optFinishTime:null //
      },
      pagination: { // 
        total: 0
      },
      tableList: [],

      form: {
        show: false, // 
        id: '',
        name: ''
      },
      dialogVisible:false,
      failReason:'',
      txId:'',
      ids:'',
      batchOperations:true,
    }
  },
  created() {
    this.list();
  },
  methods: {
    exportData(){//
      var url = "/saasHbseller/sellerRebate/statics/tx/points/list/ExportData";
      var xhr = new XMLHttpRequest();
      // var formData = new FormData();
      // for(var attr in this.search) {
      //   formData.append(attr, this.search[attr]);
      // }
      var formData = JSON.stringify(this.search)
      xhr.overrideMimeType("text/plain; charset=x-user-defined");
      xhr.open('POST', url, true);
      xhr.responseType = "blob";
      xhr.responseType = "arraybuffer";
      xhr.setRequestHeader('Content-Type','application/json');
      xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
      xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
      xhr.onload = function(res) {
        if (this.status == 200) {
          var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
          var respHeader = xhr.getResponseHeader("Content-Disposition");
          var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
          if (window.navigator.msSaveOrOpenBlob) {
            navigator.msSaveBlob(blob, fileName);
          } else {
            var link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.download = fileName;
            link.click();
            window.URL.revokeObjectURL(link.href);
          }
        }
      }
      xhr.send(formData);
    },
    handleClickReTry(){
      let ids = []
      this.tableList.forEach(item=>{
        if(item.status == 6){
          ids.push(item.id)
        }
      })
      console.log(ids)
      if(ids.length == 0){
        this.$message({
          message: '',
          type: 'warning'
        });
        return
      }
      let params = {
        ids:ids.join(','),
        type:6
      }
      this.$request.post('/saasHbseller/sellerRebate/tx/failRetry',params,false,res => {
        if(res.code == "200" ){
          this.$message({
            message: '',
            type: 'success'
          });
          this.list()
        }else{
          this.$message({
            message: res.msg,
            type: 'warning'
          });
        }
      })
    },
    handleClickFailNotes(msg){

    },
    handleSelectionChange(val) {
      let arr = val
      console.log(arr)
      let idsArr = []
      if(arr.length > 0 ){
        this.batchOperations = false
        arr.map((e,i) => {
          idsArr.push(e.id)
        })
        this.ids = idsArr.join(",")
      }else{
        this.ids = ""
        this.batchOperations = true
      }

    },
    approval(sellerId){
      let params = {ids:sellerId}
      this.$confirm(``, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$request.post('/saasHbseller/sellerRebate/tx/points/agree',params,false,res => {
          if(res.code == "200" && !res.data.failList.length){
            this.$message({
              message: '',
              type: 'success'
            });
            this.list()
          }else{
            this.$message({
              message: res.msg ? res.msg : `id${res.data.failList.join(',')}`,
              type: 'warning'
            });
          }
        })
      })
    },
    disapproval(){
      let params = {id:this.txId,authNote:this.failReason}
      this.$confirm(``, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$request.post('/saasHbseller/sellerRebate/tx/points/unAgree',params,false,res => {
          if(res.code == 200){
            this.$message({
              message: '',
              type: 'success'
            });
            this.dialogVisible = false
            this.failReason = ""
            this.list()
          }else{
            console.log(res)
            this.$message({
              message: res.msg,
              type: 'warning'
            });
            this.dialogVisible = false
            this.failReason = ""
            this.list()
          }
        })
      })
    },
    modifyAuthOrder(ids,batch) {
      let params = {ids:ids}
      let _batch = ""
      if(batch){
        _batch = ""
      }
      this.$confirm(`${_batch}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // this.$request.post('/sc/saotx/mall/order/authOrder',params,true,res => {
        this.$request.post('/saasHbseller/sellerRebate/tx/redpack/agree',params,false,res => {
          if(res.code == "200" && !res.data.failList.length){
            this.$message({
              message: '',
              type: 'success'
            });
            this.list()
          }else{
            this.$message({
              message: res.msg || `id ${res.data.failList.join(',')}`,
              type: 'warning'
            });
          }
        })
      })
    },
    disapprovalDialog(id){
      this.dialogVisible = true
      this.txId = id
    },
    cancelDialog(){
      this.dialogVisible = false
      this.failReason = ""
    },
    currentChange(page) {
      // pageNo
      this.list(event, page);
    },
    sizeChange(pageSize) {
      // pageNo
      this.list(event, null, pageSize);
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
      this.pagination.total = page;
    },
    // 
    reset() {
      this.search = {
        page: 1,
        pageSize: 10,
        contactPhone: "", //
        licenseNo: "", //
        status:null, //
        txTime:null, //
        optFinishTime:null //
      }
      this.list();
    },
    // 
    list(_event, page, pageSize) {
      let _pageNo = 1;
      if(page) _pageNo = page;
      this.search.page = _pageNo;
      let _pageSize = 10;
      if(pageSize) _pageSize = pageSize;
      this.search.pageSize = _pageSize;

      this.$request.post('/saasHbseller/sellerRebate/statics//tx/points/list', this.search, true, (res)=>{
        console.log(res.records)
        this.tableList = res.records || [];
        this.initPagination(res.total||0);
        if(this.search.status == 1){
          this.showSelection = true
        }else {
          this.showSelection = false
        }
      });
    },
    modifyData(actCode, status) {
      this.$confirm(`${status == 0 ? '':''} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = {actCode:actCode, status:status}
        this.$request.post('/saasHbseller/seller/actRank/statModify', params, false, (res)=>{
          if (res.code == '200') {
            this.list();
            this.$message({type: 'success', message: '!'});
          } else {
            this.$message.error(res.msg);
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.space {position:relative;width:100%;height:20px;}
.el-input, .el-select {
  width: 200px;
}
</style>



