<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-button size="small" type="primary" @click="dataForm('',0)"></el-button>
      </el-row>
      <!--  -->
    </el-card>
    <div class="space"></div>
    <el-card>
      <!--  -->
      <el-table :data="tableList" style="width: 100%">
        <el-table-column label="" type="index" align="center">

        </el-table-column>
        <el-table-column prop="actName" label="" align="center"></el-table-column>
        <el-table-column prop="actCode" label="code" align="center"></el-table-column>
        <el-table-column prop="stime" label="" align="center">
          <template slot-scope="scope"  width="300">
            {{ scope.row.stime }}{{ scope.row.etime }}
          </template>
        </el-table-column>
        <el-table-column prop="actJoinEtime" label="" align="center"></el-table-column>
        <el-table-column prop="outStatus" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.outStatus==0"></span>
            <span v-if="scope.row.outStatus==1"></span>
            <span v-if="scope.row.outStatus==2"></span>
            <span v-if="scope.row.outStatus==3">()</span>
            <span v-if="scope.row.outStatus==4">()</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="220">
          <template slot-scope="scope">
            <span style="color:#409EFF;cursor: pointer;margin: 0 5px" v-if="scope.row.outStatus==1" size="mini" @click="modifyData(scope.row.actCode, 3)"></span>
            <span style="color:#409EFF;cursor: pointer;margin: 0 5px" v-if="scope.row.outStatus==1||scope.row.outStatus==2||scope.row.outStatus==3" size="mini" @click="dataForm(scope.row.actCode, 1)"></span>
            <span style="color:#409EFF;cursor: pointer;margin: 0 5px" v-if="scope.row.outStatus==3" size="mini" @click="modifyData(scope.row.actCode, 1)"></span>
            <span style="color:red;cursor: pointer;margin: 0 5px" v-if="scope.row.outStatus==3 || scope.row.outStatus==4" size="mini" @click="modifyData(scope.row.actCode, 0)"></span>
            <span style="color:#409EFF;cursor: pointer;margin: 0 5px" v-if="scope.row.outStatus==4" size="mini" @click="dataForm(scope.row.actCode, 2)"></span>
          </template>
        </el-table-column>
      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                     @size-change="sizeChange" @current-change="currentChange"
                     :current-page="search.page" :page-size="search.pageSize"
                     :total="pagination.total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: {
        page: 1,
        pageSize: 10,
      },
      pagination: { // 
        total: 0
      },
      tableList: [],

      form: {
        show: false, // 
        id: '',
        name: ''
      }
    }
  },
  created() {
    this.list();
  },
  methods: {
    currentChange(page) {
      // pageNo
      this.list(event, page);
    },
    sizeChange(pageSize) {
      // pageNo
      this.list(event, null, pageSize);
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
        this.pagination.total = page;
    },
    // 
    reset() {
      this.search = {
        page: 1,
        pageSize: 10,
      }
      this.list();
    },
    // 
    list(_event, page, pageSize) {
      let _pageNo = 1;
      if(page) _pageNo = page;
      this.search.page = _pageNo;
      let _pageSize = 10;
      if(pageSize) _pageSize = pageSize;
      this.search.pageSize = _pageSize;

      this.$request.post('/saasHbseller/seller/actRank/list', this.search, true, (res)=>{
        if (res.code == '200') {
          this.tableList = res.data.list.records || [];
          this.initPagination(res.data.list.total||0);
        }
      });
    },
    dataForm(actCode, type) {
      this.$router.push(`/customerAct/rankConf?actCode=${actCode}&type=${type}`)
    },
    modifyData(actCode, status) {
      let t = ''
      if(status == 0) t =''
      if(status == 1) t =''
      if(status == 3) t =''
      this.$confirm(`${t} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = {actCode:actCode, status:status}
        this.$request.post('/saasHbseller/seller/actRank/statModify', params, false, (res)=>{
          if (res.code == '200') {
            this.list();
            this.$message({type: 'success', message: '!'});
          } else {
            this.$message.error(res.msg);
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.space {position:relative;width:100%;height:20px;}
.el-input, .el-select {
  width: 200px;
}
</style>



